<template>
  <div>
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/goods' }">商品列表</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片内容区 -->
    <el-card>
      <!-- 消息弹出框 -->
      <el-alert title="添加商品信息" type="info" show-icon center> </el-alert>
      <!-- 进度条 -->
      <el-steps :active="Number(activeName)" align-center>
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <!-- 标签页区域 -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-position="top"
      >
        <el-tabs
          tab-position="left"
          v-model="activeName"
          :before-leave="beforeTabLeave"
          @tab-click="tabClick"
        >
          <el-tab-pane label="基本信息" name="1">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model.trim="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input v-model.number="addForm.goods_price"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input v-model.number="addForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input v-model.number="addForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="商品分类" prop="goods_cat">
              <el-cascader
                v-model="addForm.goods_cat"
                :options="cateList"
                :props="propsDefault"
                @change="handleChange"
                placeholder="请选择商品分类"
                clearable
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品参数" name="2">
            <el-form-item
              :label="item.attr_name"
              v-for="item in manyTableData"
              :key="item.attr_id"
            >
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  :label="itemVal"
                  v-for="(itemVal, i) in item.attr_vals"
                  :key="i"
                  border
                  size="small"
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品属性" name="3">
            <el-form-item
              :label="item.attr_name"
              v-for="item in onlyTableData"
              :key="item.attr_id"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="4">
            <el-upload
              action="http://127.0.0.1:8888/api/private/v1/upload"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :on-success="handleSuccess"
              :before-remove="beforeRemove"
              list-type="picture"
              :headers="headerObj"
            >
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="商品内容" name="5">
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
            <el-button type="primary" @click="addGood">添加商品</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!-- 图片预览区域 -->
    <el-dialog title="图片预览" :visible.sync="picPreviewVisible" width="50%">
      <img :src="picPreviewPath" class="imgPreview" />
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'

export default {
  created() {
    this.getCateList()
  },
  data() {
    return {
      // 标签页和步骤条当前活动项
      activeName: '1',
      // 添加商品信息对象
      addForm: {
        goods_name: '',
        goods_price: null,
        goods_weight: null,
        goods_number: null,
        goods_cat: [],
        pics: [],
        goods_introduce: '',
        attrs: []
      },
      // 商品信息验证规则
      addFormRules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' },
          { type: 'number', message: '商品价格必须为数字值' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' },
          { type: 'number', message: '商品重量必须为数字值' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' },
          { type: 'number', message: '商品数量必须为数字值' }
        ],
        goods_cat: [
          { required: true, message: '请选择商品分类', trigger: 'change' }
        ]
      },
      // 商品分类列表
      cateList: [],
      // 级联选择器的配置对象
      propsDefault: {
        expandTrigger: 'hover',
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      // 动态参数数据
      manyTableData: [],
      // 静态属性数据
      onlyTableData: '',
      // 图片上传请求头
      headerObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 图片预览对话框出现
      picPreviewVisible: false,
      // 图片预览路径
      picPreviewPath: ''
    }
  },
  methods: {
    // 获取商品分类数据
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品分类数据列表失败')
      }
      this.cateList = res.data
    },
    // 监听级联选择器的变化
    handleChange() {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },
    // 当三级分类选中的情况下，才允许页签跳转
    beforeTabLeave(activeName, oldActiveName) {
      if (oldActiveName === '1' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('请选择商品分类')
        return false
      }
    },
    // 根据要跳转到的页签，执行不同的操作
    async tabClick() {
      if (this.activeName === '2') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: {
              sel: 'many'
            }
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('获取动态参数列表失败')
        }
        res.data.forEach(
          item =>
            (item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : [])
        )
        this.manyTableData = res.data
      } else if (this.activeName === '3') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: {
              sel: 'only'
            }
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('获取动态参数列表失败')
        }
        this.onlyTableData = res.data
      }
    },
    // 控制图片预览
    handlePreview(file) {
      if (file.response.meta.status !== 200) {
        return this.$message.error('预览商品图片失败')
      }
      this.picPreviewVisible = true
      this.picPreviewPath = file.response.data.url
    },
    // 控制图片删除
    handleRemove(file) {
      if (file.response.meta.status !== 200) {
        return this.$message.error('删除商品图片失败')
      }
      const picPath = file.response.data.tmp_path
      const index = this.addForm.pics.findIndex(item => item.pic === picPath)
      this.addForm.pics.splice(index, 1)
    },
    // 图片上传成功的操作
    handleSuccess(response) {
      if (response.meta.status !== 200) {
        return this.$message.error('上传商品图片失败')
      }
      this.addForm.pics.push({
        pic: response.data.tmp_path
      })
    },
    // 删除图片确认
    beforeRemove(file) {
      return this.$confirm(`确定移除 ${file.name}？`)
    },
    // 添加商品
    async addGood() {
      this.$refs.addFormRef.validate(valid => {
        if (!valid) {
          return this.$message.error('请输入必要的表单项')
        }
      })
      const form = _.cloneDeep(this.addForm)
      form.goods_cat = form.goods_cat.join(',')
      this.manyTableData.forEach(item => {
        form.attrs.push({
          attr_id: item.attr_id,
          attr_value: item.attr_vals.join(' ')
        })
      })
      this.onlyTableData.forEach(item => {
        form.attrs.push({
          attr_id: item.attr_id,
          attr_value: item.attr_vals
        })
      })
      const { data: res } = await this.$http.post('goods', form)
      if (res.meta.status !== 201) {
        return this.$message.error('添加商品失败')
      }
      this.$message.success('添加商品成功')
      this.$router.push('/goods')
    }
  },
  computed: {
    // 当前选中的第三级分类的id
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      } else {
        return null
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-checkbox {
  margin: 0 10px 10px 0 !important;
}
.imgPreview {
  width: 100%;
}
.quill-editor {
  margin-bottom: 15px;
}
::v-deep .ql-editor {
  min-height: 300px;
}
</style>
